
.jd_theme__content{
      margin-top: 5rem;
}

.container.content{
      display: flex;
      gap: 2rem;
}

.jd_content__col{
      width: calc(50% - 1rem);
}

.jd_content__description p{
      font-size: 1rem;
}

.jd_content__social{
      margin-top: 2rem;
}

.jd_content__social h2{
      font-size: 1.3rem;
}

.jd_social__menu{
      margin-top: 2rem;
      display: flex;
      gap: 5px;
}

.jd_social__menu a,
.jd_social__menu a p{
      width: 34px;
      height: 34px;
      display: flex;
      align-items: center;
      justify-content: center;
}

.jd_social__menu a{
      background: var(--theme);
      color: var(--light);
      font-size: 1.2rem;
}

.jd_content__col.video{
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      z-index: 10;
}

.jd_video__wrapper{
      width: 400px;
      height: 234px;
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
}

.jd_video__wrapper:before,
.jd_video__wrapper:after{
      width: 30px;
      height: 30px;
      border-top: solid 1px var(--theme);
      top: 0;
}

.jd_video__wrapper:before{
      left: 0;
      border-left: solid 1px var(--theme);
}

.jd_video__wrapper:after{
      border-right: solid 1px var(--theme);
      right: 0;
}

.jd_corner{
      width: 100%;
      height: 30px;
      border-left: solid 1px var(--theme);
      border-right: solid 1px var(--theme);
      position: absolute;
      bottom: 0;
      left: 0;
}

.jd_corner:before,
.jd_corner:after{
      width: 30px;
      height: 1px;
      background: var(--theme);
      bottom: 0;
}

.jd_corner:before{
      left: 0;
}

.jd_corner:after{
      right: 0;
}

.jd_video__box{
      width:380px;
      height: 214px;
      background: var(--dark-300);
      position: relative;
}

.jd_video__box:after{
      width: 60%;
      height:  50px;
      border-radius: 50%;
      background: var(--theme);
      left: 50%;
      bottom: -25px;
      transform: translateX(-50%);
      opacity: 1;
      filter: blur(25px);
      z-index: -1;
      animation: smooke 2s ease-in-out infinite;
}

@keyframes smooke{
      0%{
            opacity: 1;
            filter: blur(25px);
      }
      50%{
            opacity: .75;
            filter: blur(35px);
      }
      100%{
            opacity: 1;
            filter: blur(25px);
      }
}

.jd_video__box iframe{
      border: none;
}