.jd_relation{
      width: 200px;
      position: relative;
}

.jd_relation_avatar{
      width: 70px;
      height: 75px;
      background: var(--theme);
      border-radius: 3px;
      position: relative;
      padding: 3px;
      margin-left: 2.5rem;
      animation: bounce_avatar 1s ease-in-out infinite;
}

.jd_relation_avatar:after{
      width: 10px;
      height: 10px;
      background: var(--theme);
      bottom: -5px;
      left: calc(50% - 5px);
      transform: rotate(45deg);
}

@keyframes bounce_avatar{
      0%{
            transform: translateY(0);
      }
      50%{
            transform: translateY(-25px);
      }
      100%{
            transform: translateY(0);
      }
}

.jd_relation__avatar_box{
      width: 64px;
      height: 64px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 3px;
      overflow: hidden;
}

.jd_relation__avatar_box img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
}

.jd_relation span{
      width: 150px;
      height: 38px;
      background: var(--dark-300);
      border-radius: 20px;
      position: absolute;
      top: 117.5px;
      left: 0;
      display: flex;
      align-items: center;
      color: var(--theme);
      font-size: 1rem;
      justify-content: flex-start;
      padding-left: 2rem;
}

.jd_relation a{
      width: 50px;
      height: 50px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      color: var(--light);
      background: var(--theme);
      position: absolute;
      top: 111px;
      left: 120px;
      font-size: 1.2rem;
}

.jd_relation a:after{
      width: 60px;
      height: 60px;
      border-top: solid 2px var(--theme);
      border-right: solid 2px transparent;
      border-bottom: solid 2px var(--theme);
      border-left: solid 2px transparent;
      border-radius: 50%;
      animation: pin 1s .5s linear infinite;
      transform: translate(-50%, -50%);
      top: 50%;
      left: 50%;
}

.jd_relation p{
      width: 50px;
      height: 50px;
      display: flex;
      align-items: center;
      justify-content: center;
}

.jd_relation a:hover p{
      animation: pulse .5s ease-in-out infinite;
}

@keyframes pulse{
      0%{
            transform: scale(1)
      }
      50%{
            transform: scale(1.3)
      }
      100%{
            transform: scale(1)
      }
}