
.jd_theme__friends{
      margin-top: 7rem;
}

.container.friend{
      display: flex;
      flex-direction: column;
      align-items: center;
}

.jd_friends__title{
      max-width: 350px;
      padding: 1rem 3rem;
      position: relative;
      border-left: solid 1px var(--theme);
      border-right: solid 1px var(--theme);
      display: flex;
      align-items: center;
      justify-content: center;
}

.jd_friends__title:before,
.jd_friends__title:after{
      width: 30px;
      height: 100%;
      border-top: solid 1px var(--theme);
      border-bottom: solid 1px var(--theme);
      top: 0;
}

.jd_friends__title:before{
      left: 0;
}

.jd_friends__title:after{
      right: 0;
}

.jd_friends__title h2{
      text-transform: uppercase;
      font-size: 3rem;
      font-family: var(--ft-1);
      font-weight: 100;
      letter-spacing: 20px;
      line-height: 2.5rem;
      padding-left: 1.5rem;
}

.jd_friend__content{
      width: 600px;
      margin-top: 4rem;
      position: relative;
}