
.slick-slide,
.slick-list{
      margin: 0 15px;
}

.slick-slider{
      position: relative;
}

.slick-arrow{
      width: 36px;
      height: 36px;
      position: absolute;
      top: 7.5rem;
      border-radius: 50%;
}

.slick-arrow:before{
      width: 10px;
      height: 10px;
      left: 50%;
      top: 50%;
      color: transparent;
      border-top: solid 2px var(--theme);
      border-left: solid 2px var(--theme);
}

.slick-arrow.slick-prev:before{
      transform: translate(-50%, -50%) rotate(-45deg);
}

.slick-arrow.slick-next:before{
      transform: translate(-50%, -50%) rotate(135deg);
}


.slick-arrow.slick-prev{
      left: -3rem;
}

.slick-arrow.slick-next{
      right: -2rem;
}

.jd_friend__card{
      width: 168px;
      min-width: 168px;
      height: 245px;
      border-radius: 4px;
      box-shadow: 0 0 0 2px var(--dark-100) inset;
      transition: .2s;
}

.jd_friend__card:hover{
      box-shadow: 0 0 0 2px var(--theme) inset;
}

.jd_card__inner{
      width: 156px;
      height: 232px;
      background: var(--dark-300);
      position: relative;
      margin: 6px;
      border-radius: 6px;
      transition: .2;
      overflow: hidden;
}

.jd_card__inner:after{
      width: 200%;
      height: 100px;
      bottom: -50px;
      left: 50%;
      transform: translateX(-50%);
      background: var(--dark-300);
      filter: blur(25px);
}

.jd_friend__card:hover .jd_card__inner:after{
      animation: move_card 2s ease-in-out infinite;
}

@keyframes move_card{
      0%{
            filter: blur(25px);
            bottom: -50px;
            background: var(--dark-300);
      }
      50%{
            filter: blur(35px);
            bottom: -60px;
            background: var(--theme);
      }
      100%{
            filter: blur(25px);
            bottom: -50px;
            background: var(--dark-300);
      }
}

.jd_card__image{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      overflow: hidden;
      border-radius: 6px;
}

.jd_card__image img{
      width: auto;
      min-width: 100%;
      height: auto;
      min-height: 100%;
      filter: grayscale(1);
      transition: .2;
}

.jd_friend__card:hover .jd_card__image img{
      filter: grayscale(0);
}

.jd_card__title{
      position: relative;
      width: 100%;
      margin-top: 150px;
      z-index: 10;
      transition: .2s;
}

.jd_friend__card:hover .jd_card__title{
      transform: translateY(-15px);
}

.jd_card__title h3{
      width: 100%;
      text-align: center;
      color: var(--light);
      font-size: 1.2rem;
      text-transform: uppercase;
      font-family: var(--ft-4);
      font-weight: 300;
}

.jd_card__inner a{
      position: absolute;
      left: 50%;
      bottom: -3rem;
      background: var(--dark-300);
      text-transform: uppercase;
      padding: .5rem 1rem;
      color: var(--theme);
      font-size: .6rem;
      font-family: var(--ft-4);
      border-radius: 3px;
      transform: translateX(-50%);
      z-index: 100;
      transition: .2s;
}

.jd_friend__card:hover .jd_card__inner a{
      bottom: 2.5rem;
}

.jd_btn_next{
      position: absolute;
      width: 40px;
      height: 40px;
}