.box_loading{
      position: fixed;
      width: 100%;
      height: 100vh;
      top: 0;
      left: 0;
      background: var(--dark-300);

      display: flex;
      align-items: center;
      justify-content: center;
      z-index: 1000;
}

.box_loading__inner{
      padding-bottom: .5rem;
      border-bottom: solid 1px var(--theme);
}

.box_loading__inner h1{
      font-weight: 300;
      text-transform: uppercase;
      color: var(--theme);
      display: flex;
      letter-spacing: 2px;
}

.box_loading__inner h1 span{
      position: relative;
      animation: load 3s ease-in-out infinite;
}

@keyframes load {
      0%{
            transform: translateY(0);
      }
      15%{
            transform: translateY(-30px);
      }
      30%{
            transform: translateY(0);
      }
      100%{
            transform: translateY(0);
      }
}