@import url('https://fonts.googleapis.com/css2?family=Advent+Pro:wght@100;200;300;400;600;700&family=Electrolize&family=Major+Mono+Display&family=Zen+Dots&display=swap');

:root{
  --bg: #222;
  --light: #fff;

  --dark-300: #15192a;
  --dark-200: #1f2334;
  --dark-100: #3a3e4f;
  --text-light: #e6e8ed;
  --text: #656b7f;
  
  --ft-1: 'Advent Pro', sans-serif;
  --ft-2: 'Electrolize', sans-serif;
  --ft-3: 'Major Mono', monospace;
  --ft-4: 'Zen Dots', cursive;
}


*{
  margin: 0;
  padding: 0;
  box-sizing: border-box; 
}

:before,
:after{
  content: '';
  position: absolute;
}

body{
  background: var(--dark-200);
  color: var(--light);
  overflow-x: hidden;
  scroll-behavior: smooth;
  height: 100vh;
  min-height: 1080px;
  font-size: 62.5%;
  font-family: var(--ft-2);
  color: var(--text);

  --theme: #795e80;
  --p-x: 0;
  --p-y: 0;
}

a, span{
  display: inline-block;
}

a{
  text-decoration: none;
}

ul{
  list-style: none;
}

img{
  width: 100%;
  object-fit: cover;
}

button{
  cursor: pointer;
  outline: none;
  border: none;
}

.tooltip[data-tooltip]:before{
  content: attr(data-tooltip);
  position: fixed;
  padding:.3rem 1rem;
  background: var(--dark-300);
  text-transform: uppercase;
  font-size: .75rem;
  color: var(--light);
  border: solid 1px var(--theme);
  opacity: 0;
  visibility: hidden;
  z-index: 99;
  transition: .2s;
}

.tooltip[data-tooltip]:hover:before{
  opacity: 1;
  visibility: visible;
  top: var(--p-y);
  left: var(--p-x);
}

.container{
  width: 980px;
  position: relative;
  height: auto;
  margin: 0 auto;
}

@keyframes pin{
  100%{
    transform: translate(-50%, -50%) rotate(360deg);
  }
}