.jd_theme__banner{
      width: 100%;
      height: 294px;
      overflow: hidden;
      position: fixed;
}

.jd_banner__content{
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
}

.jd_banner__content:after{
      width: 100%;
      height: 100%;
      background: linear-gradient(rgba(31,34,52,.75) 20%, rgba(31,35,52,1) 100%);
      top: 0;
      left: 0;
}