.jd_theme__header{
      width: 100%;
      height: auto;
      display: inline-block;
      margin-top: 104px;
}

.container.header{
      display: flex;
      justify-content: space-between;
}

.jd__header_user{
      display: flex;
      gap: 1.2rem;
      position: relative;
}

.jd_user__avatar{
      width: 158px;
      height: 158px;
      position: relative;
      margin: 40px;
      border-radius: 50%;
      box-shadow: 0 0 0 40px var(--dark-300);
}

.jd_user__avatar:after{
      width: 110%;
      height: 110%;
      border-radius: 50%;
      border-top: solid 2px var(--theme);
      border-left: solid 2px transparent;
      border-bottom: solid 2px var(--theme);
      border-right: solid 2px transparent;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      animation: pin 3s linear infinite;
}

.jd_user__avatar_box{
      width: 100%;
      height: 100%;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      overflow: hidden;
}

.jd_user__data{
      margin-top: 40px;
}

.jd_user__data h2{
      font-size: 2rem;
      font-family: var(--ft-4);
      color: var(--text-light);
      font-weight: 300;
}

.jd_user__data p{
      font-size: 1rem;
}

.jd_user__data strong{
      font-size: 1.4rem;
      color: var(--theme);
      font-weight: bold;
      margin: .5rem 0;
      display: block;
}

.jd_data__location{
      display: flex;
      align-items: flex-end;
      font-size: 2rem;
      color: var(--theme);
}

.jd_data__location span{
      font-size: 1.2rem;
      color: var(--text);
}

.jd_data__find{
      position: absolute;
      width: 227px;
      height: 38px;
      background: var(--theme);
      display: flex;
      align-items: center;
      justify-content: flex-end;
      bottom: .5rem;
      left: 120px;
      z-index: -1;
}

.jd_data__find a{
      color: var(--light);
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: flex-end;
      padding-right: 2rem;
      text-transform: uppercase;
      font-size: .8rem;
}